export default {
	name: "pingLun",
	data() {
		return {
			pinglun:0,
			value:""
		};
	},
	props:{
		pinglData:{
			type: Array
		}
	},
	onLoad() {
		
	},
	methods:{
		pltext(e){
			this.value = e.target.value;
		},
		fabBtn(){
			if(this.value == ""){
				uni.showModal({
					title: '提示',
					content: '评论不能为空！',
					showCancel: false
				});
			}else{
				this.$emit('getdata',this.value);
				this.value = "";
				uni.showToast({
					title: '发表成功！',
					duration: 2000
				});
			}
		},
	}
}
